export default {
	data() {
		return {
			name: '',
			phone: '',
			email: '',
			content: ''
		}
	},
	methods: {
		submit() {
			if(this.name == '') {
				this.$vux.toast.text('请输入姓名')
				return
			}
			if(this.phone == '') {
				this.$vux.toast.text('请输入电话号码')
				return
			}
			if(this.email == '') {
				this.$vux.toast.text('请输入邮箱地址')
				return
			}
			if(this.content == '') {
				this.$vux.toast.text('请输入内容')
				return
			}
			if(!this.$refs.phone.valid) {
				this.$vux.toast.text('手机号码格式错误')
				return
			}
			if(!this.$refs.email.valid) {
				this.$vux.toast.text('邮箱格式错误')
				return
			}
			var data = {
				name: this.name,
				phone: this.phone,
				email: this.email,
				note: this.content
			}
			this.$axios(this.$api.addSuggest , data , 'post')
			.then(res =>{
				if(res.error_code == 0){
					this.$vux.toast.text('提交成功')
					this.$router.go(-1)
					this.list = res.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		}
	}
}



